import { Component, OnInit, OnDestroy } from '@angular/core';
import { FirebaseService } from '../firebase.service';
import { Introduction } from '../../interface/about-me/Introduction';
import { HttpErrorResponse } from '@angular/common/http';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import { ObserableService } from '../obserable.service';

@Component({
  selector: 'app-about-me',
  templateUrl: './about-me.component.html',
  styleUrls: ['./about-me.component.css'],
  animations: [
    trigger('spinner', [
      state('inactive', style({
        transform: 'scale(1)'
      })),
      state('active', style({
        opacity: '0',
        transform: 'scale(1)'
      })),
      transition('inactive => active', animate('500ms ease-in'))
    ]),
    trigger('card', [
      state('inactive', style({
        opacity: '0',
        transform: 'scale(1)'
      })),
      state('active', style({
        opacity: '1',
        transform: 'scale(1)'
      })),
      transition('inactive => active', animate('500ms ease-in'))
    ])
  ]
})

export class AboutMeComponent implements OnInit, OnDestroy {
  private allIntroducton: Introduction;
  private loadingAllInteroduction: Boolean = true;
  private spinnerState = 'inactive';
  private cardState = 'inactive';

  constructor(private fbService: FirebaseService, private obsService: ObserableService) { }

  ngOnInit() {
    this.loadingAllInteroduction = true;
    this.fbService.getAllIntroducton().subscribe(
      (data: Introduction) => {
        this.loadingAllInteroduction = false;
        this.allIntroducton = data;
        this.loadingOk();
      },
      (error: HttpErrorResponse) => {
        this.loadingAllInteroduction = false;
        console.log(error.message);
        this.loadingOk();
      }
    );
  }

  loadingOk() {
    this.spinnerState = 'active';
    this.cardState = 'active';
  }

  ngOnDestroy() {
    this.obsService.leftMenuOppen.next(false);
  }

}
